import React, { useState } from "react";
import Minuse from "../../common/preloader/minuse";
import Pluse from "../../common/preloader/pluse";
import Preloader from "../../common/preloader/preloader";
import classes from './ProfileInfo.module.css';
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import userPhoto from "../../../assets/imges/user.png";
import ProfileDataForm from "./ProfileDataForm";

const ProfileInfo = ({ profile, isOwner, savePhoto, saveProfile, status, updateStatus }) => {

    let [editMode, setEditMode] = useState(false);
    if (!profile) {
        return <Preloader />
    }
    const onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
            savePhoto(e.target.files[0]);
        }
    }
    const onSubmit = (formData) => {
        saveProfile(formData).then(
            () => {
                setEditMode(false);
            }
        )

    }

    return (
        <div>
            {/* <div>
                <img className={classes.mainPhoto} src="https://wallpaperaccess.com/full/781752.jpg" alt="" />
            </div> */}

            <div className={classes.descriptionBlock}>

                <img className={classes.Photo} src={profile.photos.large || userPhoto} alt="" />

                <ProfileStatusWithHooks status={status} updateStatus={updateStatus} />


                {editMode
                    ? <ProfileDataForm initialValues={profile} profile={profile} onSubmit={onSubmit} />
                    : <ProfileData goToEditMode={() => setEditMode(true)} profile={profile} isOwner={isOwner} />}


            </div>
            {isOwner && <input className={classes.photoBtn} type={'file'} onChange={onMainPhotoSelected} />}

        </div>

    )
}

const ProfileData = ({ profile, isOwner, goToEditMode, status, updateStatus }) => {

    return <div className={classes.description}>

        {isOwner && <div><button onClick={goToEditMode}>edit</button></div>}


        <div className={classes.fullName}>{profile.fullName}</div>
        <div>Ищу работу:{(profile.lookingForAJob
            ? <Pluse className={classes.pluse} />
            : <Minuse className={classes.minuse} />)}
        </div>
        {profile.lookingForAJob &&
            <div>
                <b>My Professional skills </b>: {profile.lookingForAJobDescription}
            </div>}
        <div><p>{profile.aboutMe}</p></div>
        <div>
            <b>Contacts</b>: {Object.keys(profile.contacts).map(key => {
                return <Contact key={key} contactTitle={key} contactValue={profile.contacts[key]} />
            })}
        </div>
    </div>
}

const Contact = ({ contactTitle, contactValue }) => {
    return (<div className={classes.contact}><b>{contactTitle}</b> : <b>{contactValue}</b></div>)
}

export default ProfileInfo;